export default class  Utils {

    static async sendActionToModel(context,action) {
        let pthis = context;
      var p = new Promise(function (resolve, reject) {
        action.callback=(ret) => {   
          //      
             resolve(ret);
        }
        pthis.props.dispatch(action);
      });
      return p;
    }

    static async dispatchActiionData(context, action) {
        if(!action.hasOwnProperty("type"))
        {
          console.error("action hava not type");
          return;
        }
        if(action.hasOwnProperty("callback"))
        {
          console.error("callback action is not allowed");
        }
        return await Utils.sendActionToModel(context,action); 
    }

    static sliceUnit(val){
      try {
        var e = val.split(" ");
        return e[0];
      } catch (error) {
        return '0.0000';
      }
    }

    static chkEosQuantity(obj) {
      obj = obj.replace(/[^\d.]/g, "");  // "" "."
      obj = obj.replace(/^\./g, "");  //
      obj = obj.replace(/\.{2,}/g, "."); //
      obj = obj
        .replace(".", "$#$")
        .replace(/\./g, "")
        .replace("$#$", ".");
      obj = obj.replace(/^(\-)*(\d+)\.(\d\d\d\d).*$/,'$1$2.$3'); //
      // var max = 9999999999.9999;  // 100 -1
      // var min = 0.0000;
      // var value = 0.0000;
      // var floatbalance;
      // try {
      //   value = parseFloat(obj);
      //   floatbalance = parseFloat(this.state.balance);
      // } catch (error) {
      //   value = 0.0000;
      //   floatbalance = 0.0000;
      // }
      // if(value < min|| value > max){
      //   EasyToast.show(Translate.getT(""));
      //   obj = "";
      // }
      // if (value > floatbalance) {
      //     EasyToast.show(Translate.getT(','));
      //     obj = "";
      // }
      return obj;
  }
}


